.button {
  display: inline-block;
  width: 100%;
  box-sizing: border-box;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  cursor: pointer;
}
.button:focus {
  outline: none;
}
.button--primary {
  max-width: 400px;
  padding: 14px;
  font-size: 18px;
  line-height: 20px;
  color: #444444;
  background-color: #ffffff;
  border: 2px solid #444444;
}
.button--primary:hover, .button--primary:focus {
  color: #ffffff;
  background-color: #444444;
}
.button--primary:active {
  color: rgba(255, 255, 255, 0.3);
}
.button--secondary {
  padding: 18px;
  font-size: 17px;
  line-height: 16px;
  color: #ffffff;
  background-color: #63d1bb;
  border: none;
}
.button--secondary:hover, .button--secondary:focus {
  color: #63d1bb;
  background-color: #ffffff;
  box-shadow: inset 0 0 0 2px #63d1bb;
}
.button--secondary:active {
  color: rgba(99, 209, 187, 0.3);
}
.button--form {
  display: block;
  max-width: 400px;
  padding: 16px;
  font-size: 17px;
  line-height: 16px;
  color: #444444;
  background-color: #ffffff;
  border: 2px solid #444444;
}
.button--form:hover, .button--form:focus {
  color: #ffffff;
  background-color: #444444;
}
.button--form:active {
  color: rgba(255, 255, 255, 0.3);
}

