.control {
  display: flex;
  align-items: center;
  gap: 19px;
  cursor: pointer;
}
.control--checkbox {
  gap: 23px;
}
@media (min-width: 768px) {
  .control {
    gap: 29px;
  }
}

.control__label {
  font-weight: 700;
  line-height: 18px;
  color: #000000;
}
@media (min-width: 768px) {
  .control__label {
    font-size: 19px;
  }
}

.control__mark {
  position: relative;
  flex-shrink: 0;
  box-sizing: border-box;
  border: 2px solid #444444;
}

.control:hover .control__mark,
.control__input:focus + .control__mark {
  border-color: #62d1ba;
}

.control:active .control__mark {
  border-color: rgba(98, 209, 186, 0.3);
}

.control--radio .control__mark {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
@media (min-width: 768px) {
  .control--radio .control__mark {
    width: 49px;
    height: 49px;
  }
}

.control--radio .control__input:checked + .control__mark::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 18px;
  height: 18px;
  margin: auto;
  border-radius: 50%;
  background-color: #62d1ba;
}
@media (min-width: 768px) {
  .control--radio .control__input:checked + .control__mark::before {
    width: 23px;
    height: 23px;
  }
}

.control--checkbox .control__mark {
  width: 38px;
  height: 38px;
}
@media (min-width: 768px) {
  .control--checkbox .control__mark {
    width: 52px;
    height: 52px;
  }
}

.control--checkbox .control__input:checked + .control__mark::before {
  content: "";
  position: absolute;
  top: 3px;
  right: 0;
  left: 0;
  width: 10px;
  height: 20px;
  margin: 0 auto;
  border-right: 2px solid #000000;
  border-bottom: 2px solid #000000;
  transform: rotate(45deg);
}
@media (min-width: 768px) {
  .control--checkbox .control__input:checked + .control__mark::before {
    top: 6px;
    width: 14px;
    height: 27px;
  }
}

