  padding: 74px 30px;
  font-weight: 700;
  font-size: 27px;
  color: #000000;
  text-align: center;
  max-width: 1500px;
  margin-right: auto;
  margin-left: auto;
}
@media (min-width: 768px) {
  .page-header__title {
    padding-top: 36px;
    padding-bottom: 87px;
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .page-header__title {
    padding-top: 37px;
    padding-bottom: 113px;
    font-size: 53px;
    line-height: 57px;
  }
}

.page-header--index-background-image .page-header__title {
  padding-top: 73px;
  padding-bottom: 120px;
}
@media (min-width: 768px) {
  .page-header--index-background-image .page-header__title {
    padding-top: 90px;
    padding-bottom: 372px;
  }
}
@media (min-width: 1150px) {
  .page-header--index-background-image .page-header__title {
    padding-top: 93px;
    padding-bottom: 355px;
  }
}

.page-header__menu {
  margin: 0;
  max-width: 1500px;
  margin-right: auto;
  margin-left: auto;
}

.page {
  height: 100%;
  background-color: #ffffff;
}

.page__body {
  min-height: 100%;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  margin: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 17px;
  line-height: 30px;
  color: #444444;
}

.product-card {
  display: grid;
  grid-template-rows: auto 1fr;
  border: 1px solid #ececec;
}
.product-card:hover, .product-card:focus-within {
  box-shadow: 0 6px 10px rgba(0, 0, 0, 0.1);
}
.product-card:active {
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);
}
@media (min-width: 768px) {
  .product-card {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: none;
  }
}
@media (min-width: 1150px) {
  .product-card {
    grid-template-columns: none;
    grid-template-rows: auto 1fr;
  }
}

.product-card__image-link {
  display: block;
  position: relative;
  min-height: 0;
  padding-bottom: 75%;
}
.product-card__image-link:hover, .product-card__image-link:focus {
  opacity: 0.8;
  outline: none;
}
.product-card__image-link:active {
  opacity: 0.5;
}
@media (min-width: 768px) {
  .product-card__image-link {
    padding-bottom: 100%;
  }
}
@media (min-width: 1150px) {
  .product-card__image-link {
    padding-bottom: 147%;
  }
}

.product-card__image {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.product-card__description-wrapper {
  display: flex;
  flex-direction: column;
  padding: 26px 15px 19px 28px;
}
@media (min-width: 768px) {
  .product-card__description-wrapper {
    padding: 26px 20px 19px 34px;
  }
}
@media (min-width: 1150px) {
  .product-card__description-wrapper {
    padding: 26px 20px 15px 27px;
  }
}

.product-card__title-link {
  display: block;
  margin-bottom: 8px;
  padding-right: 13px;
}
.product-card__title-link:hover, .product-card__title-link:focus {
  color: rgba(0, 0, 0, 0.6);
  outline: none;
}
.product-card__title-link:active {
  color: rgba(0, 0, 0, 0.3);
}

.product-card__title {
  margin: 0;
  font-size: inherit;
}

.product-card__description {
  margin: 0;
  margin-bottom: 17px;
  padding-right: 13px;
  font-size: 13px;
}
@media (min-width: 768px) {
  .product-card__description {
    font-size: 15px;
  }
}
@media (min-width: 1150px) {
  .product-card__description {
    margin-bottom: 13px;
  }
}

.product-card__price-wrapper {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-top: auto;
  padding-top: 10px;
}
.product-card__price-wrapper::before {
  content: "";
  position: absolute;
  top: 0;
  right: 13px;
  left: 0;
  height: 1px;
  background-color: #ececec;
}

.product-card__price {
  margin-bottom: 7px;
  font-weight: 700;
  font-size: 15px;
  line-height: 24px;
  color: #000000;
}
@media (min-width: 768px) {
  .product-card__price {
    font-size: 17px;
  }
}

.product-card__cart {
  display: block;
  padding: 12px;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.product-card__cart:focus {
  outline: none;
}

.product-card__cart-icon {
  color: rgba(0, 0, 0, 0.2);
}

.product-card__cart:hover .product-card__cart-icon,
.product-card__cart:focus .product-card__cart-icon {
  color: #63d1bb;
}

.product-card__cart:active .product-card__cart-icon {
  color: rgba(99, 209, 187, 0.3);
}

.production {
  padding: 0 30px;
}
@media (min-width: 768px) {
  .production {
    padding: 0 50px;
  }
}

.production__wrapper {
  display: grid;
  border: 1px solid #ececec;
}
.production__wrapper:hover, .production__wrapper:focus-within {
  box-shadow: 0 6px 10px rgba(0, 0, 0, 0.1);
}
.production__wrapper:active {
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);
}
@media (min-width: 1150px) {
  .production__wrapper {
    grid-template-columns: 65% 35%;
    grid-template-rows: min-content 1fr;
  }
}

.production__title {
  margin: 21px 34px 26px 33px;
  padding-left: 57px;
  background-image: url("../img/icons/video.svg");
  background-repeat: no-repeat;
  background-position: 0 50%;
}
@media (min-width: 768px) {
  .production__title {
    margin: 40px 34px 0 41px;
    padding-top: 2px;
    padding-left: 64px;
    background-position: 0 0;
  }
}
@media (min-width: 1150px) {
  .production__title {
    padding: 52px 15px 0 0;
    margin: 44px 36px 0 79px;
  }
}

.production__video-container {
  position: relative;
  width: 100%;
  min-height: 0;
  padding-bottom: 58%;
}
@media (min-width: 768px) {
  .production__video-container {
    order: -1;
    padding-bottom: 68%;
  }
}
@media (min-width: 1150px) {
  .production__video-container {
    grid-row: 1/3;
    padding-bottom: 66.5%;
  }
}

.production__video-preview {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.production__video-button {
  position: absolute;
  top: 50%;
  left: 50%;
  display: block;
  width: 101px;
  height: 77px;
  border: none;
  outline: none;
  background-color: transparent;
  background-image: url("../img/icons/play-button.svg");
  background-repeat: no-repeat;
  background-size: 100%;
  transform: translate(-50%, -50%);
  cursor: pointer;
}
.production__video-button:hover, .production__video-button:focus {
  width: 121px;
  height: 92px;
}
.production__video-button:active {
  width: 101px;
  height: 77px;
  opacity: 30%;
}

.production__description-wrapper {
  padding: 34px 32px 32px 33px;
}
@media (min-width: 768px) {
  .production__description-wrapper {
    padding: 23px 41px 45px 41px;
  }
}
@media (min-width: 1150px) {
  .production__description-wrapper {
    padding: 26px 55px 45px 79px;
  }
}

.production__description {
  margin: 0;
  margin-bottom: 41px;
  font-size: 13px;
  line-height: 24px;
}
@media (min-width: 768px) {
  .production__description {
    font-size: 15px;
    line-height: 30px;
  }
}
@media (min-width: 1150px) {
  .production__description {
    margin-bottom: 52px;
  }
}

.production__order {
  max-width: none;
}

.products {
  padding: 0 30px;
}
@media (min-width: 768px) {
  .products {
    padding: 0 50px;
  }
}

.products__list {
  display: grid;
  gap: 30px;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (min-width: 768px) {
  .products__list {
    gap: 53px;
  }
}
@media (min-width: 1150px) {
  .products__list {
    grid-template-columns: repeat(3, 1fr);
    gap: 51px;
  }
}

.reviews {
  position: relative;
  padding: 0 30px 51px;
}
@media (min-width: 768px) {
  .reviews {
    padding: 0 50px 87px 0;
  }
}
@media (min-width: 1150px) {
  .reviews {
    padding: 0 50px 91px 50px;
  }
}
.reviews::after {
  content: "";
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  height: 6px;
  background-image: url("../img/index/zigzag-line.svg");
  background-repeat: repeat-x;
  background-size: auto 6px;
}
@media (min-width: 768px) {
  .reviews::after {
    height: 17px;
    background-image: url("../img/index/zigzag-line.svg");
    background-size: auto 17px;
  }
}
@media (min-width: 1150px) {
  .reviews::after {
    right: 50px;
    left: 50px;
    height: 23px;
    background-size: auto 23px;
  }
}

@media (min-width: 768px) {
  .reviews__wrapper {
    display: grid;
    grid-template-areas: "title write-button" "reviews reviews" "reviews reviews";
    grid-template-columns: auto 175px;
    padding: 78px 0 50px 50px;
    background-image: url("../img/index/index-review-bg.jpg");
    background-repeat: no-repeat;
    background-size: auto 100%;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .reviews__wrapper {
    background-image: url("../img/index/index-review-bg@2x.jpg");
  }
}
@media (min-width: 1150px) {
  .reviews__wrapper {
    padding: 78px 0 98px 175px;
  }
}

.reviews__title {
  margin-bottom: 14px;
}
@media (min-width: 768px) {
  .reviews__title {
    grid-area: title;
    margin-top: 3px;
    margin-bottom: 39px;
  }
}
@media (min-width: 1150px) {
  .reviews__title {
    margin-top: 0;
    margin-bottom: 35px;
  }
}

@media (min-width: 768px) {
  .reviews__list {
    grid-area: reviews;
  }
}

.reviews__item {
  display: none;
  margin: 0;
}
.reviews__item--current {
  display: block;
}

.reviews__text {
  margin: 0;
  margin-bottom: 33px;
  font-size: 15px;
}
@media (min-width: 768px) {
  .reviews__text {
    margin-bottom: 61px;
    font-size: 17px;
    text-shadow: 0 -1px 0 rgba(255, 255, 255, 0.75), 0 1px 0 rgba(255, 255, 255, 0.75), -1px 0 0 rgba(255, 255, 255, 0.75), 1px 0 0 rgba(255, 255, 255, 0.75), -1px -1px 0 rgba(255, 255, 255, 0.75), 1px -1px 0 rgba(255, 255, 255, 0.75), -1px 1px 0 rgba(255, 255, 255, 0.75), 1px 1px 0 rgba(255, 255, 255, 0.75);
  }
}
@media (min-width: 1150px) {
  .reviews__text {
    margin-bottom: 40px;
  }
}

.reviews__author-name {
  display: block;
  margin-bottom: 8px;
  font-style: normal;
  font-weight: 700;
  line-height: 24px;
  color: #000000;
}
@media (min-width: 768px) {
  .reviews__author-name {
    font-size: 18px;
    padding-right: 175px;
  }
}
@media (min-width: 1150px) {
  .reviews__author-name {
    font-size: 17px;
    line-height: 30px;
  }
}

.reviews__author-nickname {
  margin: 0;
  margin-bottom: 34px;
  line-height: 20px;
}
@media (min-width: 768px) {
  .reviews__author-nickname {
    margin-bottom: 3px;
    padding-right: 175px;
  }
}
@media (min-width: 1150px) {
  .reviews__author-nickname {
    margin-bottom: 0;
  }
}

.reviews__buttons {
  position: relative;
  display: flex;
  max-width: 260px;
  margin: 0 auto 38px auto;
  border: 1px solid #ececec;
}
@media (min-width: 768px) {
  .reviews__buttons {
    grid-column: 2/3;
    grid-row: 3/4;
    max-width: 175px;
    margin-bottom: 0;
    border: none;
  }
}
@media (max-width: 767.9px) {
  .reviews__buttons::before {
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    left: 50%;
    width: 1px;
    background-color: #ececec;
  }
}

.reviews__button {
  padding: 25px 64px;
  border: none;
  background-color: #ffffff;
  cursor: pointer;
}
.reviews__button:focus {
  outline: none;
}
.reviews__button--prev {
  padding: 12px 42px 12px 37px;
}
@media (min-width: 768px) {
  .reviews__button--prev {
    padding: 12px 18px 12px 19px;
  }
}
.reviews__button--next {
  padding: 12px 41px 12px 38px;
}
@media (min-width: 768px) {
  .reviews__button--next {
    padding: 12px 18px 12px 19px;
  }
}

.reviews__button-icon {
  display: block;
  color: #000000;
}

.reviews__button:hover .reviews__button-icon,
.reviews__button:focus .reviews__button-icon {
  color: rgba(0, 0, 0, 0.6);
}

.reviews__button:active .reviews__button-icon {
  color: rgba(0, 0, 0, 0.3);
}

.reviews__write {
  display: block;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .reviews__write {
    grid-area: write-button;
    align-self: start;
    max-width: 175px;
    margin-bottom: 2px;
  }
}

.size-form {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .size-form {
    padding: 0 15px;
  }
}

.size-form__title {
  align-self: center;
  margin: 0;
  margin-bottom: 22px;
  font-size: 15px;
}
@media (min-width: 768px) {
  .size-form__title {
    margin-bottom: 20px;
    font-size: 17px;
  }
}

.size-form__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 7px;
  margin: 0;
  margin-bottom: 31px;
  padding: 0;
  list-style-type: none;
}

.size-form__item {
  flex: 1;
}

.size-form__label {
  display: block;
  padding: 16px;
  border: 2px solid rgba(68, 68, 68, 0.1);
  font-weight: 700;
  line-height: 16px;
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;
}
.size-form__label:hover {
  border: 2px solid rgba(68, 68, 68, 0.3);
}

.size-form__input:focus + .size-form__label {
  border: 2px solid rgba(68, 68, 68, 0.3);
}

.size-form__input:checked + .size-form__label {
  border: 2px solid #62d1ba;
}

@media (max-width: 767.9px) {
  .social {
    padding: 26px 0 28px;
  }
}

.social__list {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 10px 29px;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.social__link {
  display: flex;
  padding: 2px 0;
}
.social__link:focus {
  outline: none;
}

.social__link-icon {
  color: #231f20;
}

.social__link:hover .social__link-icon,
.social__link:focus .social__link-icon {
  color: #46c1ae;
}

.social__link:active .social__link-icon {
  opacity: 0.3;
}

